/* Custom CSS */
@import url('https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


*{
  font-family: 'Exo 2', sans-serif;
  /* outline: red solid 1px; */
}

@layer{
  p{
      @apply  xl:text-xl 2xl:text-2xl xxs:text-base text-sm;
  }
}

.textGradient{
  background: #556FF7;
  background: -o-linear-gradient(left, #556FF7 0%, #8FF4FF 100%);
  background: -webkit-gradient(linear, left top, right top, from(#556FF7), to(#8FF4FF));
  background: linear-gradient(to right, #556FF7 0%, #8FF4FF 100%);
  color: transparent;
  -webkit-background-clip: text;
          background-clip: text;
}

.btn-primary{
  padding: 0.65rem 1.3rem;
  border-radius: 0.75rem/* 12px */;
  font-size: 0.85rem/* 12px */;
  line-height: 1rem/* 16px */;
  background-image: linear-gradient(200deg, #5a78ab, rgba(142, 97, 255, 0) 34%), radial-gradient(circle farthest-corner at 0% -100%, #619bff 20%, rgba(205, 219, 248, 0) 51%), linear-gradient(180deg, #556ff7, #556ff7);
  box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.9), 0 0 15px 0 rgba(39, 1, 255, 0.07), 0 5px 53px 0 #5570f700;
  -webkit-transition: all 250ms ease, box-shadow 200ms ease, -webkit-filter 2000ms cubic-bezier(.165, .84, .44, 1);
  transition: all 250ms ease, box-shadow 200ms ease, -webkit-filter 2000ms cubic-bezier(.165, .84, .44, 1);
  transition: all 250ms ease, filter 2000ms cubic-bezier(.165, .84, .44, 1), box-shadow 200ms ease;
  transition: all 250ms ease, filter 2000ms cubic-bezier(.165, .84, .44, 1), box-shadow 200ms ease, -webkit-filter 2000ms cubic-bezier(.165, .84, .44, 1);
  color: #fff;
  color: #fff;
  cursor: pointer;
}


.btn-primary:not(.btn-primary:disabled):hover{
  border-style: none;
  border-width: 1px;
  border-color: hsla(0, 0%, 100%, 0.26);
  box-shadow: 5px 0 30px 0 rgba(0, 0, 0, 0.1);
  -webkit-filter: hue-rotate(-60deg);
  filter: hue-rotate(-60deg);
  transition: 350ms ease-in-out;
  -webkit-transform: scale(1.05);
  -ms-transform: scale(1.05);
  transform: scale(1.05);
}

.btn-primary:disabled{
  cursor:not-allowed;
  padding: 0.65rem 1.3rem;
  border-radius: 0.75rem/* 12px */;
  font-size: 0.75rem/* 12px */;
  line-height: 1rem/* 16px */;
  background: rgb(107 114 128);
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
  position: relative;
  z-index: 2;
  display: block;
  background-color: rgb(107 114 128);
  box-shadow: none;

  background-image: linear-gradient(200deg, #e1e1e1, rgba(142, 97, 255, 0) 34%), radial-gradient(circle farthest-corner at 0% -100%, #619bffce 5%, rgba(205, 219, 248, 0) 51%), linear-gradient(180deg, rgb(107 114 128), rgb(107 114 128));
  -webkit-transition: all 250ms ease, box-shadow 200ms ease, -webkit-filter 2000ms cubic-bezier(.165, .84, .44, 1);
  transition: all 250ms ease, box-shadow 200ms ease, -webkit-filter 2000ms cubic-bezier(.165, .84, .44, 1);
  transition: all 250ms ease, filter 2000ms cubic-bezier(.165, .84, .44, 1), box-shadow 200ms ease;
  transition: all 250ms ease, filter 2000ms cubic-bezier(.165, .84, .44, 1), box-shadow 200ms ease, -webkit-filter 2000ms cubic-bezier(.165, .84, .44, 1);
  text-align: center;
  cursor: default;
}

.btn-secondary{
  padding: 0.65rem 1.3rem;
  border: 1px solid #222;
  border-radius: 0.5rem/* 8px */;
  text-align: center;
  cursor: pointer;
}

.btn-secondary:hover, .btn-secondary:focus{
  cursor: pointer;
  transform: scale(102.5%);
  border-color: #556ff7;
  background-color: hsla(230, 91%, 15%, 0.155);
  color: #556ff7;
  transition-property: all;
  transition: 350ms ease-in-out;
}

