/* Reset Slider */
#slider{
  -webkit-appearance: none;
  width: 100%;
  height: 10px;
  outline: none;
  border-radius: 1.5rem/* 8px */;
  box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.16)
  inset 1px 1px 1px #909090;
}

/* Reset Thumb */
#slider::-webkit-slider-thumb{
  -webkit-appearance: none;
  width: 1.75rem;
  height: 1.75rem;
  border: 1px solid rgba(34, 34, 34, 0.05);
  cursor: pointer;
  z-index: 100;
  position: relative;
  border-radius: 50%;
  background-color: #556FF7;
  box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0), 0 0 15px 0 rgba(39, 1, 255, 0), 0 5px 53px 0 #5570f700;
}
#slider::-moz-range-thumb{
  -webkit-appearance: none;
  width: 1.75rem;
  height: 1.75rem;
  border: 1px solid rgba(34, 34, 34, 0.05);
  cursor: pointer;
  z-index: 100;
  position: relative;
  border-radius: 50%;
  background-color: #556FF7;
  box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0), 0 0 15px 0 rgba(39, 1, 255, 0), 0 5px 53px 0 #5570f700;
}

#slider::-ms-thumb{
  -webkit-appearance: none;
  width: 1.75rem;
  height: 1.75rem;
  border: 1px solid rgba(34, 34, 34, 0.05);
  cursor: pointer;
  z-index: 100;
  position: relative;
  border-radius: 50%;
  background-color: #556FF7;
  box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0), 0 0 15px 0 rgba(39, 1, 255, 0), 0 5px 53px 0 #5570f700;
}

#slider::-o-thumb{
  -webkit-appearance: none;
  width: 1.75rem;
  z-index: 100;
  height: 1.75rem;
  border: 1px solid rgba(34, 34, 34, 0.05);
  cursor: pointer;
  z-index: 10;
  position: relative;
  border-radius: 50%;
  background-color: #556FF7;
  box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0), 0 0 15px 0 rgba(39, 1, 255, 0), 0 5px 53px 0 #5570f700;
}

/* Hover Chrome - Safari  */
#slider::-webkit-slider-thumb:hover, #slider::-webkit-slider-thumb:focus, #slider::-webkit-slider-thumb:active{
  box-shadow: 1px 1px 3px 5px rgba(0, 0, 0, 0.19), 0 0 15px 0 rgba(39, 1, 255, 0.75), 0 5px 53px 10px #5570f7;
  -webkit-transition: all 250ms ease, box-shadow 200ms ease, -webkit-filter 2000ms cubic-bezier(.165, .84, .44, 1);
  width: 2rem;
  height: 2rem;
  transition: all 350ms ease-in-out;
  cursor: grabbing;
}

/* Hover Moz */
#slider::-moz-range-thumb:hover, #slider::-moz-range-thumb:focus, #slider::-moz-range-thumb:active{
  box-shadow: 1px 1px 3px 5px rgba(0, 0, 0, 0.19), 0 0 15px 0 rgba(39, 1, 255, 0.75), 0 5px 53px 10px #5570f7;
  -webkit-transition: all 250ms ease, box-shadow 200ms ease, -webkit-filter 2000ms cubic-bezier(.165, .84, .44, 1);
  width: 2rem;
  height: 2rem;
  transition: all 350ms ease-in-out;
  cursor: grabbing;
}

/* Hover IE */
#slider::-ms-thumb:hover, #slider::-ms-thumb:focus, #slider::-ms-thumb:active{
  box-shadow: 1px 1px 3px 5px rgba(0, 0, 0, 0.19), 0 0 15px 0 rgba(39, 1, 255, 0.75), 0 5px 53px 10px #5570f7;
  -webkit-transition: all 250ms ease, box-shadow 200ms ease, -webkit-filter 2000ms cubic-bezier(.165, .84, .44, 1);
  width: 2rem;
  height: 2rem;
  transition: all 350ms ease-in-out;
  cursor: grabbing;
}

/* Hover Opera */
#slider::-o-thumb:hover, #slider::-o-thumb:focus, #slider::-o-thumb:active{
  box-shadow: 1px 1px 3px 5px rgba(0, 0, 0, 0.19), 0 0 15px 0 rgba(39, 1, 255, 0.75), 0 5px 53px 10px #5570f7;
  -webkit-transition: all 250ms ease, box-shadow 200ms ease, -webkit-filter 2000ms cubic-bezier(.165, .84, .44, 1);
  width: 2rem;
  height: 2rem;
  transition: all 350ms ease-in-out;
  cursor: grabbing;
}

.slider-container{
  position: relative;
}

.slider-container .bar{
  position: absolute;
  z-index: 1;
  left: 0;
  width: 100%;
  height: 14px;
  background-color: #5570f756;
  border-radius: 1.5em/* 8px */;
  top: 50%;
  transform: translateY(-50%);

  overflow: hidden;
}

.slider-container .bar .fill{
  width: 0;
  height: 100%;
  position: absolute;
  bottom: 0;
  background-image: linear-gradient(200deg, #5a78ab, rgba(142, 97, 255, 0) 34%), radial-gradient(circle farthest-corner at 0% -100%, #619bff 20%, rgba(205, 219, 248, 0) 51%), linear-gradient(180deg, #556ff7, #556ff7);
  border-radius: 1.5;
  border-bottom-left-radius: 1.5;
}
